import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { SeriesComponent } from 'app/components/series/series.component';
import { SerieDetailsComponent } from 'app/components/series/series-details.component';


const serieRoutes: Routes = [
    { path: 'series', component: SeriesComponent },
    { path: 'series/:id', component: SerieDetailsComponent }
]

@NgModule({
    imports: [
        CommonModule, RouterModule.forChild(serieRoutes)
    ],
    exports: [RouterModule],
    declarations: []
})
export class SeriesRoutingModule { }



