import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap, NavigationEnd } from '@angular/router';
import { SeriesService } from 'app/services/series.service';
@Component({
    selector: 'serie-details',
    templateUrl: './series-details.component.html',
})

export class SerieDetailsComponent implements OnInit {
    serie: any = [];
    constructor(private serieService: SeriesService, private activedRoute: ActivatedRoute) { }
    ngOnInit() {
        console.log('work')
        this.getSerie();
    }
    getSerie() {
        const paramId = this.activedRoute.snapshot.params['id'];
        this.serieService.getSerie(paramId).subscribe(response => this.serie = response.serie[0],
            error => {
                console.log('Get serie method :: Something wrong ')
            }, () => {
                console.log(this.serie)
            });
    }
};


