import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpModule } from '@angular/http';
import { SeriesComponent } from 'app/components/series/series.component';
import { SeriesRoutingModule } from 'app/components/series/serieRouting.module';
import { SharedModule } from 'app/components/shared.module';
import { SerieDetailsComponent } from 'app/components/series/series-details.component';


@NgModule({
    imports: [
        CommonModule, HttpModule, SeriesRoutingModule, SharedModule
    ],
    declarations: [SeriesComponent, SerieDetailsComponent],
    providers: []
})
export class SeriesModule { }



